@import url(https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f2f2f2;
}

:root {
  font-size: 10px;
  --color-light: #f2f2f2;
  --color-white: white;
  --color-gray: #cccccc;
  --color-complementary: #b52a2a;
  --color-black: black;
  --font-default: "Poppins";
}

.App {
  text-align: left;
  margin: 0 auto;
}

.magnifying-glass {
  color: #b52a2a;
  font-size: 10rem;
  padding-left: 30px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
}
.magnifying-glass:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

@media (max-width: 730px) {
  .magnifying-glass {
    font-size: 8rem;
  }
}

.exclamation {
  display: block;
  color: #b52a2a;
  padding-bottom: 20px;
  font-size: 16rem;
}

@media (max-width: 730px) {
  .exclamation {
    font-size: 12rem;
  }
}

